let expertise = [
    {
        title: 'Digital Services',
        icon: exp1,
        text: [
            { text: 'Accessible Websites', id: 1 },
            { text: 'Custom WordPress', id: 2 },
            { text: 'Web Applications', id: 3 },
            { text: 'Business Portals', id: 4 },
            { text: 'Application  Integration', id: 5 },
            { text: 'Search Engine Optimization', id: 6 },
            { text: 'Performance & Security', id: 7 },
            { text: 'Website Rescue', id: 8 },
            { text: 'Online Marketing', id: 9 },
            { text: 'e-commerce', id: 10 },
            { text: 'Content Managment', id: 11 },
            { text: 'Hosting & Site Security', id: 12 },
        ],
        id: 1
    },
    {
        title: 'Design Services',
        icon: exp2,
        text: [
            { text: 'End-to-end Graphic Design', id: 13 },
            { text: 'Creative Strategy', id: 14 },
            { text: 'Brand Identity ', id: 15 },
            { text: 'Print Solutions', id: 16 },
            { text: 'Large Format Printing', id: 17 },
            { text: 'Social Media', id: 18 },
            { text: 'Digital Advertising', id: 19 },
            { text: 'Key Position & Messaging', id: 20 },
            { text: 'Interactive Design', id: 21 },
            { text: 'Content Development', id: 22 },
            { text: 'Package Design', id: 23 },
            { text: 'Interface and Navigation', id: 24 },
        ],
        id: 2
    },
    {
        title: 'Workflow Strategies',
        icon: exp3,
        text: [
            { text: 'Marketing Strategy', id: 25 },
            { text: 'Conent Writing', id: 26 },
            { text: 'Technology Strategy', id: 27 },
            { text: 'Business Process', id: 28 },
            { text: 'Email and Office Administration ', id: 29 },
            { text: 'Managment', id: 30 },
            { text: 'Tradeshows and Events', id: 31 },
            { text: 'Promotional Material', id: 32 },
            { text: 'System Integrations', id: 33 },
            { text: 'Emerging Technologies', id: 34 },
            { text: 'Thinking Beyond', id: 35 }
        ],
        id: 3
    }
]

let clients = [
    { logo: client1, website: 'www.cmccanada.org', id: 1 },
    { logo: client2, website: 'www.cmccanada.org', id: 2 },
    { logo: client3, website: 'www.cmccanada.org', id: 3 },
    { logo: client4, website: 'www.cmccanada.org', id: 4 },
    { logo: client5, website: 'www.cmccanada.org', id: 5 },
    { logo: client6, website: 'www.cmccanada.org', id: 6 },
    { logo: client7, website: 'www.cmccanada.org', id: 7 },
    { logo: client8, website: 'www.cmccanada.org', id: 8 }
]

let approach = [
    {
        title: 'Listening to your goals',
        text: 'Sustainable solutions stems from a deep dive into understanding the evolving needs of your business',
        lottie: {
            loop: false,
            autoplay: false,
            animationData: approach1,
            rendererSettings: {
                preserveAspectRatio: 'xMidYMid slice'
            },
        },
        id: 1
    },
    {
        title: 'Dive into your workflow',
        text: 'Sustainable solutions stems from a deep dive into understanding the evolving needs of your business',
        lottie: {
            loop: false,
            autoplay: false,
            animationData: approach2,
            rendererSettings: {
                preserveAspectRatio: 'xMidYMid slice'
            },
        },
        id: 2
    },
    {
        title: 'Propsing ideal scenarios',
        text: 'Sustainable solutions stems from a deep dive into understanding the evolving needs of your business',
        lottie: {
            loop: false,
            autoplay: false,
            animationData: approach3,
            rendererSettings: {
                preserveAspectRatio: 'xMidYMid slice'
            },
        },
        id: 3
    },
    {
        title: 'Listening to your goals',
        text: 'Sustainable solutions stems from a deep dive into understanding the evolving needs of your business',
        lottie: {
            loop: false,
            autoplay: false,
            animationData: approach4,
            rendererSettings: {
                preserveAspectRatio: 'xMidYMid slice'
            },
        },
        id: 4
    },
    {
        title: 'Dive into your workflow',
        text: 'Sustainable solutions stems from a deep dive into understanding the evolving needs of your business',
        lottie: {
            loop: false,
            autoplay: false,
            animationData: approach5,
            rendererSettings: {
                preserveAspectRatio: 'xMidYMid slice'
            },
        },
        id: 5
    },
    {
        title: 'Propsing ideal scenarios',
        text: 'Sustainable solutions stems from a deep dive into understanding the evolving needs of your business',
        lottie: {
            loop: false,
            autoplay: false,
            animationData: handshake,
            rendererSettings: {
                preserveAspectRatio: 'xMidYMid slice'
            },
        },
        id: 6
    }
]

export default function About({ headerRef, handleHover, handleLeave, togglePopup, testimonialsGallery }) {
    let mobileScreen = window.innerWidth < 990;
    const visionImageElement = useRef(null);
    const visionColumn = useRef(null);
    const valuesWrapper = useRef(null);
    const phones = useRef([]);
    const phonesBanner = useRef(null);
    const expertiseTitle = useRef([]);
    const approachWrapper = useRef(null);
    const approachText = useRef([]);
    const expertiseWrapper = useRef(null);
    const approachList = useRef(null);
    const clientsWrapper = useRef(null);
    const sections = useRef([]);
    const links = useRef([]);
    let loaded = useContext(LoadedContext);

    const [valueIconsStopped, setValueIconsStopped] = useState([true, true, true, true]);
    const [approachIconsStopped, setApproachIconsStopped] = useState([true, true, true, true, true, true]);
    const [count, setCount] = useState(0);
    const [smallScreen, setSmallScreen] = useState(false);

    function handleMobileScreen() {
        if (window.innerWidth < 480) {
            setSmallScreen(true);
        } else {
            setSmallScreen(false);
        }
    }

    function initPhonesAnimation() {
        let tl = gsap.timeline({
            defaults: {
                ease: 'Power1.in'
            },
            scrollTrigger: {
                trigger: phonesBanner.current,
                start: 'center 80%',
            }
        })
        phones.current.forEach((phone, index) => {
            if (index === 0) {
                tl.to(phone, {
                    left: 0,
                }, 0)
            } else if (index === 2) {
                tl.to(phone, {
                    right: 0,
                }, 0)
            }
        });
    }

    function toggleValueAnimation(index) {
        setValueIconsStopped(valueIconsStopped.map((item, itemIndex) => (index === itemIndex) ? !valueIconsStopped[index] : true));
    }

    function toggleApproachAnimation(index) {
        setApproachIconsStopped(approachIconsStopped.map((item, itemIndex) => (index === itemIndex) ? !approachIconsStopped[index] : true));
    }

    function onLinkClick(e) {
        e.preventDefault();
        const target = e.nativeEvent.target.getAttribute('href');
        let section = document.getElementById(target);
        gsap.to(window, {
            scrollTo: section.getBoundingClientRect().top + window.scrollY - headerRef.current.clientHeight - 141,
            ease: 'Power4.inOut',
            duration: 0.5
        });
    }

    let onSlideEnd = useCallback(() => {
        let interval = setInterval(() => {
            setCount((state) => {
                if (state === 19) {
                    clearInterval(interval);
                }
                return state + 1
            });
        }, 50)
    }, []);

    useEffect(() => {
        if (loaded) {
            handleMobileScreen();
            initPhonesAnimation();
            animateTextLine(expertiseTitle.current);
            animateTextLine(approachText.current);
            parallaxAnimation(visionImageElement.current, 40, 'top bottom', visionColumn.current);
            setActiveOnScroll(sections.current, links.current);

            window.addEventListener('resize', handleMobileScreen);
        }

        return () => {
            window.removeEventListener('resize', handleMobileScreen);
        }
    }, [loaded]);
    return (
        <main className="main about">
            {loaded ? <PageProgress /> : null}
            <ParallaxBanner
                video={video}
                title="About Us"
                animateTitle={true}
                className="banner--about"
                percent={50}
                headerRef={headerRef}
                upperTitle="Creative studio design"
                scalePercent={1.3}
            />
            <div className="about__wrapper">
                <CustomParticles modificator="about" />
                <div className="about__vision container" id="vision-wrapper">
                    <SideText modificator="vision" wrapper="vision-wrapper">our vision</SideText>
                    <div className="about__vision-column about__vision-column--1">
                        <SlideIn setXPercent={-100} toXPercent={0} trigger="#vision-wrapper">
                            <h2 className="about__vision-title">100% client focused and 100% co-operative</h2>
                            <p className="about__vision-text">
                                Thinkbound prides itself on being a dedicated team of professionals all with value-added expertise and high-levels of qualifications. As a full-service interactive strategic partner, we afford our clients the added advantage of seamless coordination between the different phases of a project, translating to faster time to market and economies of scale.
                                <br />
                                <br />
                                Our approach allows us a constant commitment to exceeding your expectations and advancing our core values of providing affordable, high quality, innovative solutions with integrity.
                            </p>
                        </SlideIn>
                    </div>
                    <div className="about__vision-column about__vision-column--2" ref={visionColumn}>
                        <SlideIn setYPercent={120} toYPercent={0} delay={0.3} trigger="#vision-wrapper" callback={onSlideEnd}>
                            <div className="about__vision-image-wrapper">
                                <img src={visionImage} className="about__vision-image" alt="" ref={visionImageElement} />
                            </div>
                            <div className="about__vision-count">
                                <strong>{count}+</strong>
                                <SlideIn setYPercent={100} toYPercent={0} delay={1} trigger="#vision-wrapper">Years of Experience</SlideIn>
                            </div>
                        </SlideIn>
                    </div>
                </div>
                <h1 className="about__title about__title--values container">Our Values</h1>
                <div className="about__values container" ref={valuesWrapper}>
                    {values.map((value, index) => {
                        return (
                            <SlideIn opacity={1} delay={index / 9} className="about__value" key={value.id} trigger={valuesWrapper.current} onMouseEnter={() => toggleValueAnimation(index)} onMouseLeave={() => toggleValueAnimation(index)}>
                                <div className="about__value-wrapper">
                                    <div className="about__value-icon-wrapper">
                                        <Lottie className="about__value-icon" options={value.lottie} isStopped={valueIconsStopped[index]} />
                                    </div>
                                    <h3 className="about__value-title">{value.title}</h3>
                                </div>
                                <p className="about__value-text">{value.text}</p>
                            </SlideIn>
                        );
                    })}
                </div>
                <div className="banner banner--phones" id="phones-banner" ref={phonesBanner}>
                    <ParallaxBackground image={phonesBg} className="banner__image-wrapper" />
                    <div className="banner__wrapper container container--header">
                        <SideText modificator="phones" wrapper="phones-banner">about us</SideText>
                        <div className="banner__column banner__column--1">
                            <SlideIn setYPercent={100} toYPercent={0} trigger={phonesBanner.current} start="center 80%" className="banner__upper-title">How we get the breakthrough</SlideIn>
                            <SlideIn setYPercent={100} toYPercent={0} trigger={phonesBanner.current} start="center 80%" className="banner__title">Thinkbound grows your business</SlideIn>
                            <SlideIn setYPercent={100} toYPercent={0} trigger={phonesBanner.current} start="center 80%" className="banner__text">
                                When thinking about the future of your business, you need a partner that will take the time to understand your needs, the needs of your leadership, your team, and your industry. A partner that can offer unique cutting edge digital solutions that raise the bar within your organization and that surpass your competition.
                                <br />
                                <br />
                                <strong>Thinkbound is your trasformation partner, implementing technologies and marketing solutions into your organization that break down boundaries to achieving higher success.</strong>
                            </SlideIn>
                        </div>
                        <div className="banner__column banner__column--2">
                            <img src={phone1} className="banner__image banner__image--1" alt="" ref={el => addToRefs(el, phones)} />
                            <img src={phone2} className="banner__image banner__image--2" alt="" ref={el => addToRefs(el, phones)} />
                            <img src={phone3} className="banner__image banner__image--3" alt="" ref={el => addToRefs(el, phones)} />
                        </div>
                    </div>
                </div>
                <div className="about__expertise container" id="expertise-wrapper">
                    <SideText modificator="expertise" wrapper="expertise-wrapper">expertise</SideText>
                    {mobileScreen ?
                        <nav className="about__expertise-nav">
                            <h3 className="about__title about__title--nav">Areas of expertise</h3>
                            <ul className="about__expertise-navlist">
                                {expertise.map(item => {
                                    return (
                                        <li className="about__expertise-navitem" key={item.id}>
                                            <a href={"link" + item.id} className="about__expertise-navlink" onClick={onLinkClick} ref={el => addToRefs(el, links)}>{item.title}</a>
                                        </li>
                                    );
                                })}
                            </ul>
                        </nav> :
                        <div className="line-text line-text--expertise about__title about__title--expertise">
                            <h3 className="line-text__row" ref={el => addToRefs(el, expertiseTitle)}>Areas of expertise</h3>
                        </div>}
                    <ul className="about__expertise-list" ref={expertiseWrapper}>
                        {expertise.map((item, index) => {
                            return (
                                <li className="about__expertise-item" key={item.id} id={"link" + item.id} ref={el => addToRefs(el, sections)}>
                                    <SlideIn setXPercent={-100} toXPercent={0} trigger={expertiseWrapper.current} delay={mobileScreen ? 0 : index / 5}>
                                        <div className="about__expertise-title">
                                            <div className="about__expertise-icon">
                                                <img src={item.icon} alt="" width="100%" />
                                            </div>
                                            <h5>{item.title}</h5>
                                        </div>
                                    </SlideIn>
                                    <div className="about__expertise-text">
                                        {item.text.map((textItem, textIndex) => {
                                            return (
                                                <SlideIn setYPercent={-200} toYPercent={0} trigger={expertiseWrapper.current} delay={mobileScreen ? 0 : textIndex / 9} className="about__expertise-text-item" key={textItem.id}>
                                                    <div className="about__expertise-text-wrapper">{textItem.text}</div>
                                                </SlideIn>
                                            );
                                        })}
                                    </div>
                                </li>
                            );
                        })}
                    </ul>
                </div>
                <h3 className="about__title about__title--small container">Some of our clients</h3>
                <div className="about__clients container" ref={clientsWrapper}>
                    {clients.map((client, index) => {
                        return (
                            <SlideIn opacity={1} delay={index / 5} trigger={clientsWrapper.current} className="about__client" key={client.id}>
                                <div className="about__client-background"></div>
                                <img src={client.logo} className="about__client-logo about__client-logo--main" alt="" />
                                <div className="about__client-hidden">
                                    <img src={client.logo} className="about__client-logo about__client-logo--small" alt="" />
                                    <a href={client.website} className="about__client-link" onMouseOver={handleHover} onMouseLeave={handleLeave}>{client.website}</a>
                                </div>
                            </SlideIn>
                        );
                    })}
                </div>
                <div className="about__approach" ref={approachWrapper} id="approach-wrapper">
                    <div className="about__approach-wrapper container">
                        <SideText modificator="approach" wrapper="approach-wrapper">approach</SideText>
                        {smallScreen ?
                            <div className="line-text line-text--approach">
                                <div className="line-text__row" ref={el => approachText.current.push(el)}>We break things down</div>
                                <div className="line-text__row" ref={el => approachText.current.push(el)}>into phases that are meaningful,</div>
                                <div className="line-text__row" ref={el => approachText.current.push(el)}>measurable and minimize cost</div>
                            </div> :
                            <div className="line-text line-text--approach">
                                <div className="line-text__row" ref={el => approachText.current.push(el)}>We break things down into phases that are meaningful,</div>
                                <div className="line-text__row" ref={el => approachText.current.push(el)}>measurable and minimize cost</div>
                            </div>}
                        <div className="about__approach-list" ref={approachList}>
                            {approach.map((item, index) => {
                                return (
                                    <SlideIn opacity={1} delay={index / 5} trigger={approachList.current} className="about__approach-item" key={item.id} onMouseEnter={() => toggleApproachAnimation(index)} onMouseLeave={() => toggleApproachAnimation(index)}>
                                        <div className="about__approach-item-icon">
                                            <Lottie options={item.lottie} isStopped={approachIconsStopped[index]} />
                                        </div>
                                        <h6 className="about__approach-item-title">{item.title}</h6>
                                        <p className="about__approach-item-text">{item.text}</p>
                                    </SlideIn>
                                );
                            })}
                        </div>
                    </div>
                    <ExpandingBackground trigger={approachWrapper} className="about__expanding-background" />
                </div>
                <Testimonials gallery={testimonialsGallery} togglePopup={togglePopup} />
            </div>
        </main>
    )
}
