import React, { useContext } from 'react'
import Lottie from 'react-lottie';
import { CursorContext } from './../providers/CursorProvider';

export default function Tabs({ className, menuItems, Component, refs, iconsStopped, toggleIcons }) {
    let { handleHover, handleLeave } = useContext(CursorContext);

    return (
        <div className={"tabs " + className}>
            <ul className="tabs__menu">
                {menuItems.map((item, index) => {
                    return (
                        <li ref={el => refs.current.push(el)} key={item.id} className="tabs__menu-item" onMouseEnter={() => toggleIcons(index)} onMouseLeave={() => toggleIcons(index)}>
                            <button className="tabs__menu-button" onMouseOver={handleHover} onMouseLeave={handleLeave}>
                                <div className="tabs__menu-icon">
                                    <Lottie options={item.lottie} isStopped={iconsStopped[index]} />
                                </div>
                                <p className="tabs__menu-text">{item.title}</p>
                            </button>
                        </li>
                    );
                })}
            </ul>
            <ul className="tabs__wrapper">
                <li className="tabs__item">
                    <Component />
                </li>
            </ul>
        </div>
    )
}
