import React, { useEffect, useRef, useState } from 'react'
import SideText from '../../../../../common/SideText';
import Tabs from '../../../../../common/Tabs';
import IndustryCard from './IndustryCard';

import gsap from 'gsap/gsap-core';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import ExpandingBackground from './../../../../../common/ExpandingBackground';

import warehouse from '../../../../../animation/warehouse.json';
import loveCare from '../../../../../animation/love-care.json';
import tech from '../../../../../animation/technology.json';

gsap.registerPlugin(ScrollTrigger);

let menuItems = [
    {
        title: 'Self Storage',
        lottie: {
            loop: false,
            autoplay: false,
            animationData: warehouse,
            rendererSettings: {
                preserveAspectRatio: 'xMidYMid slice'
            },
        },
        id: 1
    },
    {
        title: 'Arts Culture',
        lottie: {
            loop: false,
            autoplay: false,
            animationData: loveCare,
            rendererSettings: {
                preserveAspectRatio: 'xMidYMid slice'
            },
        },
        id: 2
    },
    {
        title: 'Hospitality',
        lottie: {
            loop: false,
            autoplay: false,
            animationData: tech,
            rendererSettings: {
                preserveAspectRatio: 'xMidYMid slice'
            },
        },
        id: 3
    }
]

export default function Industry() {
    let menuItemRefs = useRef([]);
    let industryWrapperRef = useRef(null);
    const [iconsStopped, setIconsStopped] = useState([true, true, true]);

    function toggleIcons(index) {
        setIconsStopped(iconsStopped.map((item, itemIndex) => (index === itemIndex) ? !iconsStopped[index] : true));
    }

    useEffect(() => {
        let menuItemElements = menuItemRefs.current;

        initMenuItemsAnimation(menuItemElements);
    }, [])

    return (
        <section className="section industry">
            <div ref={industryWrapperRef} className="industry__wrapper container" id="industry-wrapper">
                <SideText modificator="industry" wrapper="industry-wrapper">industries</SideText>
                <h1 className="industry__title">
                    Industry Solutions.<br /> Be next!
                </h1>
                <Tabs refs={menuItemRefs} className="tabs--industry" menuItems={menuItems} Component={IndustryCard} iconsStopped={iconsStopped} toggleIcons={toggleIcons} />
            </div>
            <ExpandingBackground className="industry__expanding-background" trigger={industryWrapperRef} />
        </section>
    )
}

function initMenuItemsAnimation(menuItemElements) {

    ScrollTrigger.create({
        trigger: menuItemElements[0],
        start: "top center",
        onEnter: () => animateMenuItems(menuItemElements)
    })
}

function animateMenuItems(menuItemElements) {

    return gsap.to(menuItemElements, {
        duration: 0.3,
        xPercent: 110,
        stagger: 0.1,
        ease: 'Power4.out'
    })
}
